  .cart .addToCart .txt {
    font-size: 12px;
    color: #fff;
    letter-spacing: 0.045em;
    text-transform: uppercase;
    white-space: nowrap;
  }





  .overlay {
    opacity: 0;
    position:fixed ;
    
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease-out;
    visibility: hidden;
    overflow: hidden;
    
    .overlayDrawer {
      position: absolute;
      width: 450px;
      height: 100%;
      right: -420px;
      padding: 30px;
      background: #ffffff;
      box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
      display: flex;
      flex-direction: column;
      transition: all 0.3s ease-out;
    }
  }